<template>
	<el-container>
		<el-main>
			<div class="table-header-btn">
				<div class="table-header-btn-l">
					<el-date-picker  v-model="Time" type="daterange" format="yyyyMMdd" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
					<el-button type="primary" size="small" @click="search()">检索</el-button>
				</div>
			</div>
			<el-table
				ref="table"
				:data="dataList"
        :height="siteContentViewHeight - 155 +'px'"
			>
				<el-table-column label="序号" align="center" width="50">
					<template slot-scope="scope"> {{pageObj.pageIndex*pageObj.pageSize+scope.$index+1}} </template>
				</el-table-column>
				<el-table-column prop="userCode" label="操作人账号" show-overflow-tooltip></el-table-column>
				<el-table-column prop="userName" label="操作人姓名" show-overflow-tooltip></el-table-column>
				<el-table-column prop="logIp" label="IP地址" show-overflow-tooltip></el-table-column>
				<el-table-column prop="sysName" label="子系统名称" show-overflow-tooltip></el-table-column>
				<el-table-column prop="logContent" label="操作内容" show-overflow-tooltip></el-table-column>
				<el-table-column prop="logTime" label="操作时间" show-overflow-tooltip ></el-table-column>
			</el-table>
			<!--分页-->
			<div>
				<pagination :pageIndex="pageObj.pageIndex" :total="pageObj.total" :pageSize="pageObj.pageSize" @PageTurning="PageTurning"></pagination>
			</div>
		</el-main>
	</el-container>
</template>

<script>
	import pagination from '@/components/baseComponents/Pagination.vue';
	export default {
		name: "operation-log-index",
		// props:['siteContentViewHeight'],
		components: {
			pagination,
		},
    props:['siteContentViewHeight'],
		data() {
			return {
			  pageObj: {
					total: 0, //数据总数
					pageIndex:this.API.constObj.pageIndex,//当前页码
					pageSize:this.API.constObj.pageSize, //页大小
        		},
        		//操作类型0:新增、1：编辑、2、删除、3：查询、4：浏览、5：密码初始化、6：其他
				// options: [
				// 	{
				// 		value: '0',
				// 		label: '新增'
				// 	}, {
				// 		value: '1',
				// 		label: '编辑'
				// 	},
				// 	{
				// 		value: '2',
				// 		label: '删除'
				// 	}, {
				// 		value: '3',
				// 		label: '查询'
				// 	},
				// 	{
				// 		value: '4',
				// 		label: '浏览'
				// 	},
				// 	{
				// 		value: '5',
				// 		label: '密码初始化'
				// 	},
				// 	{
				// 		value: '6',
				// 		label: '其他'
				// 	}
        		// ],
        		dataList:[],
				type: "", //操作类型
				uCreator: "", //操作人姓名
				Time: [], //时间
				startTime: "",     //开始时间
				endTime: "",      //结束时间
				tableHeight: 0,//给tabale设置高度
			}
		},
		created() {
      		this.getDataList()
		},
		watch:{
			dataList:{
				handler:function(newVal){
					this.tableHeight = window.innerHeight - this.$refs.table.$el.offsetTop - 184;
				}
			}
		},
		methods: {

			//获取数据列表
			getDataList(){
				this.$http({
					url: this.$http.adornUrl(this.API.queryOperateLogSysoperatelog),
					method: 'post',
					data: this.$http.adornData({
						startDate: this.startTime,
						endDate: this.endTime,
						size:this.pageObj.pageSize,
						current:this.pageObj.pageIndex + 1,
					})
				}).then((res) =>{
					var resData = res.data;
					if(resData.code === 0) {
						this.dataList = resData.data.records;
						this.pageObj.total = resData.data.total;
					} else {
						this.$message.error(resData.msg);
					}
				})
			},

			//搜索
			search() {
				if(this.Time === null){
					this.startTime = "";
					this.endTime = "";
				}else if((this.Time || this.Time != "") && this.Time.length > 0){
					let strDate = this.Time;
					this.startTime = this.commonFun.dateFormat(strDate[0], "yyyy-MM-dd");
					this.endTime = this.commonFun.dateFormat(strDate[1], "yyyy-MM-dd");
				}else{
					this.startTime = "";
					this.endTime = "";
				}
				this.pageObj.pageIndex = this.API.constObj.pageIndex;
				this.getDataList();
			},

			//分页组件回调方法
			PageTurning(page_obj) {
				this.pageObj.pageIndex = page_obj.page_index;
				this.pageObj.pageSize = page_obj.page_size;
				this.getDataList();
			},
		}
	}
</script>

<style lang="scss" scoped>
  .el-main {
    background-color: #fff;
    padding:0 !important;
    .table-header-btn {
		.el-select{
			width: 200px;
			margin:0 10px 0 0;
		}
		.el-date-editor--daterange.el-input__inner{
			width:400px;
		}
		.el-button{
			margin:0 0 0 10px;
		}
	}

  }
  .sysLogOption_page {
    margin-top: 10px;
    text-align: right;
  }
</style>



